import UILoader from "@components/ui-loader"
import {
  Loader,
  NoDataDisplay,
  ErrorFetchData
} from "@src/shared/CommonComponents"
import { getDataList } from "@src/shared/dynamic_pages/store"
import "moment-timezone"
import "moment/locale/id"
import { useEffect, useState } from "react"
import { useTranslation } from "react-i18next"
import { useDispatch, useSelector } from "react-redux"
import { useNavigate } from "react-router-dom"
import { Button, Card, CardBody, CardHeader, CardTitle, Col } from "reactstrap"

import { Swiper, SwiperSlide } from "swiper/react/swiper-react"

import img2 from "@src/assets/images/banner/banner-19.jpg"
import img1 from "@src/assets/images/banner/banner-20.jpg"
import { Fullscreen, FullscreenExit } from "@mui/icons-material"
import { renderIcon } from "@src/views/apps/menu/store/my_menu"

const params = {
  spaceBetween: 30,
  centeredSlides: true,
  autoplay: {
    delay: 7000,
    disableOnInteraction: true
  },
  autoHeight: true,
  pagination: {
    clickable: true,
    type: "bullets",
    bulletClassName: "custom-bullet"
  },
  navigation: false
}

const WidgetAnnouncement = ({ lg, md }) => {
  const { t } = useTranslation()
  // const store = useSelector((state) => state[storeName])
  const store = useSelector((state) => state.dynamicStore)
  const dispatch = useDispatch()
  const navigate = useNavigate()

  const storeName = "widget_announcement"
  const endpointName = "api/announcement"

  const [isFullscreen, setIsFullscreen] = useState(false)

  // const toggleFullscreen = () => {
  //   if (!isFullscreen) {
  //     if (divElement.requestFullscreen) {
  //       divElement.requestFullscreen()
  //     } else if (divElement.mozRequestFullScreen) {
  //       divElement.mozRequestFullScreen()
  //     } else if (divElement.webkitRequestFullscreen) {
  //       divElement.webkitRequestFullscreen()
  //     } else if (divElement.msRequestFullscreen) {
  //       divElement.msRequestFullscreen()
  //     }
  //   } else {
  //     if (
  //       document.fullscreenElement ||
  //       document.mozFullScreenElement ||
  //       document.webkitFullscreenElement ||
  //       document.msFullscreenElement
  //     ) {
  //       if (document.exitFullscreen) {
  //         document.exitFullscreen()
  //       } else if (document.mozCancelFullScreen) {
  //         document.mozCancelFullScreen()
  //       } else if (document.webkitExitFullscreen) {
  //         document.webkitExitFullscreen()
  //       } else if (document.msExitFullscreen) {
  //         document.msExitFullscreen()
  //       }
  //     }
  //   }

  //   setIsFullscreen(!isFullscreen)
  // }

  const requestFullscreen = () => {
    const element = document.getElementById("announcement-widget-div")
    if (element.requestFullscreen) {
      element.requestFullscreen()
    } else if (element.mozRequestFullScreen) {
      element.mozRequestFullScreen()
    } else if (element.webkitRequestFullscreen) {
      element.webkitRequestFullscreen()
    } else if (element.msRequestFullscreen) {
      element.msRequestFullscreen()
    }
    element.style.zoom = 1.4
    setIsFullscreen(true)
  }

  const exitFullscreen = () => {
    if (
      document.fullscreenElement ||
      document.mozFullScreenElement ||
      document.webkitFullscreenElement ||
      document.msFullscreenElement
    ) {
      if (document.exitFullscreen) {
        document.exitFullscreen()
      } else if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen()
      } else if (document.webkitExitFullscreen) {
        document.webkitExitFullscreen()
      } else if (document.msExitFullscreen) {
        document.msExitFullscreen()
      }

      const element2 = document.getElementById("announcement-widget-div")
      if (element2) {
        element2.style.zoom = 1
      }
    }

    setIsFullscreen(false)
  }

  useEffect(() => {
    const fetchData = () => {
      if (
        !store.pageData[storeName] ||
        (store.pageData[storeName]?.data?.length === 0 &&
          !store.pageData[storeName]?.isLoading)
      ) {
        dispatch(
          getDataList({
            storeName: storeName,
            endpointName: endpointName,
            baseUrl: "sso"
          })
        )
      }
    }

    const fetchDataTimeout = setTimeout(fetchData, 5000)

    const handleKeydown = (event) => {
      if (document.fullscreenElement === null) {
        const element2 = document.getElementById("announcement-widget-div")
        if (element2) {
          element2.style.zoom = 1
        }
        exitFullscreen()
      }
    }

    document.addEventListener("fullscreenchange", handleKeydown)
    return () => {
      document.removeEventListener("fullscreenchange", handleKeydown)
      clearTimeout(fetchDataTimeout)
    }
  }, [isFullscreen])

  const imgs = [img1, img2, img1, img2]
  {
    return store.pageData[storeName]?.isLoading ? (
      <Col lg={lg ?? "12"} xs="12" md={md ?? "12"}>
        <Card style={{ minHeight: "200px" }}>
          <CardHeader>
            <div className="d-flex align-items-center">
              {renderIcon(
                "material:campaign",
                `user-timeline-title-icon me-1`,
                25
              )}
              <CardTitle
                // style={{
                //   padding: "20px 20px 5px 20px",
                //   fontSize: "1.3rem"
                // }}
                tag="h4"
              >
                {t("Announcements")}
              </CardTitle>
            </div>
          </CardHeader>
          <UILoader blocking={true} loader={<Loader />}>
            <CardBody style={{ minHeight: "200px" }}></CardBody>
          </UILoader>
        </Card>
      </Col>
    ) : store.pageData[storeName]?.data?.length > 0 ? (
      <Col lg={lg ?? "12"} xs="12" md={md ?? "12"}>
        <Card id="announcement-widget-div" className="card-announcements-slide">
          <CardBody
            className="text-center p-0"
            style={{
              cursor: "pointer"
            }}
          >
            <Button.Ripple
              onClick={!isFullscreen ? requestFullscreen : exitFullscreen}
              className="btn-icon btn-announcement-fs"
              color="flat-secondary"
            >
              {!isFullscreen ? (
                <Fullscreen size={16} />
              ) : (
                <FullscreenExit size={16} />
              )}
            </Button.Ripple>

            <Swiper {...params}>
              {store.pageData[storeName]?.data.map((slide, index) => {
                return (
                  <SwiperSlide key={`swiper_${slide.id}`}>
                    <div
                      key={index}
                      style={{
                        backgroundImage: `url(${
                          slide.img_path ?? imgs[index] ?? img1
                        })`,
                        backgroundSize: "cover",
                        backgroundPosition: "center",
                        height: !isFullscreen ? "340px" : "100vh",
                        display: "flex",
                        flexDirection: "column",
                        alignItems: "center",
                        borderRadius: "8px"
                      }}
                    >
                      <h3
                        style={{
                          color: "white",
                          padding: "20px 20px 5px 20px",
                          fontSize: "1.3rem"
                        }}
                      >
                        {slide.title}
                      </h3>
                      <div
                        className="slide-container-announcement"
                        style={{
                          color: "white",
                          textAlign: "center",
                          padding: "5px 20px 0 20px",
                          maxHeight: !isFullscreen ? "228px" : "100vh"
                        }}
                        dangerouslySetInnerHTML={{
                          __html: slide?.description
                        }}
                      ></div>
                    </div>
                    <div className="slide-overlay"></div>
                    <Button.Ripple
                      onClick={(e) => {
                        navigate(
                          `/dyn_pages/_admin_/my_announcements/view/${slide.id}?refresh=1&isOrigin=true`
                        )
                      }}
                      className="round btn-announcement-card"
                      color="secondary"
                    >
                      {t("Read more")}...
                    </Button.Ripple>
                  </SwiperSlide>
                )
              })}
            </Swiper>
          </CardBody>
        </Card>
      </Col>
    ) : (
      <Col lg={lg ?? "12"} xs="12" md={md ?? "12"}>
        <Card
          className="card-announcements-slide-no-data"
          style={{ minHeight: "200px" }}
        >
          <CardHeader>
            <div className="d-flex align-items-center">
              {renderIcon(
                "material:campaign",
                `user-timeline-title-icon me-1`,
                25
              )}
              <CardTitle tag="h4">{t("Announcements")}</CardTitle>
            </div>
          </CardHeader>
          <CardBody>
            <NoDataDisplay
              customStyles={{
                minHeight: "200px"
              }}
              titleCustom={t("There has been no announcement.")}
              messageCustom={t(
                "There is currently no announcement. Later, if there is an announcement, it will appear here."
              )}
            />
          </CardBody>
        </Card>
      </Col>
    )
  }
}

export default WidgetAnnouncement
